export default class AppView {
  constructor(clips) {
    this.clips = clips;
  }

  render(page = 1) {
    const clipsContainer = document.getElementById('clipsContainer');
    let pagesContainer = document.body.querySelector('.pagesContainer');
    if (document.body.contains(pagesContainer)) document.body.removeChild(pagesContainer);
    clipsContainer.innerHTML = '';
    const content = document.createElement('ul');
    content.classList.add('content');
    content.innerHTML = this.clips.map((clip) => {
      if (clip.videoPage === page) {
        return `<li class="clip">
        <div class="clip-div">
          <img src="${clip.img}" class="clip-img"/>
          <div class="link-container">
            <a href="https://www.youtube.com/watch?v=${clip.videoId}" target="_blank">${clip.title}</a>
          </div>
        </div>
        <div class="clip-info">
          <div><img class="clip-icon" src="data:image/svg+xml;utf8;base64," />${clip.channelTitle}</div>
          <div><img class="clip-icon" src="data:image/svg+xml;utf8;base64," />${clip.publishedAt}</div>
          <div><img class="clip-icon" src="data:image/svg+xml;utf8;base64," />${clip.viewsCount}</div>
          <p class="clip-description">${clip.description}</p>
        </div>
      </li>`;
      } return '';
    }).join('');
    content.style.visibility = 'hidden';
    clipsContainer.appendChild(content);
    setTimeout(() => {
      content.style.visibility = 'visible';
    }, 10);
    pagesContainer = document.createElement('div');
    pagesContainer.classList.add('pagesContainer');
    const pages = document.createElement('ul');
    pages.id = 'ulPages';
    pages.classList.add('pages');
    if (page === 1) pages.innerHTML = '<li class="active-page" id="active">1</li><li class="nonactive-page" id="choosePage">2</li>';
    else if (page === 2) pages.innerHTML = '<li class="nonactive-page" id="prev">1</li><li class="active-page" id="active">2</li><li class="nonactive-page" id="next">3</li>';
    else pages.innerHTML = `<li class="nonactive-page" id="prevPrev">${page - 2}</li><li class="nonactive-page" id="prev">${page - 1}</li><li class="active-page" id="active">${page}</li><li class="nonactive-page" id="next">${page + 1}</li>`;
    pagesContainer.appendChild(pages);
    document.body.appendChild(pagesContainer);
  }
}
